.main {
  display: flex;
  justify-content: center;
  .box {
    width: 200px;
    height: 200px;
    background-color: #006E90;
    position: relative;

    &:after {
    content: '';
    width: 20px;
    height: 20px;
    background-color: #006E90;
    position: absolute;
    bottom: -15px;
    transform: rotate(45deg) translateX(-50%);
    left: 50%;
    }
  }
}
